    cols    1     2      3
    
    px      68    160    252    */
    
    
    
    @media only screen and (max-width: 767px) {
    
    :root
    {
    --fnt16:16px;
    --fnt18:18px;
    --fnt20:20px;
    --fnt22:22px;
    --fnt30:22px;
    --fnt40:40px;
    --fnt50:40px;
    --fnt70:70px;
    --fnt80:80px;
    --fnt100:100px;
    
    
    }
    
    
    .banner_artwork
    {
            display: none;
    }
    .top_banner
    {background: rgb(68,53,146);
          
    background: linear-gradient(0deg, rgba(68,53,146,1) 0%, rgba(22,13,87,1) 16%, rgba(28,52,132,1) 33%, rgba(7,73,101,1) 48%, rgba(23,55,119,1) 60%, rgba(39,58,120,1) 77%, rgba(52,36,122,1) 94%);
    }
    .logo
    {
            width: 120px;
    }
    .mobile_open_data
    {
            padding: 30px 0px;
    }
    .middle_text
    {
                margin-top: 50px;
