        padding-top: 10px;
    }
    .mt-70-mobile
    
    {
            margin-top: 70px!important;
    }
    .marquee_text_main {
        overflow: hidden;
        margin-top: 80px;
        padding-bottom: 30px;
    }
    
    .mobile_border_fix
    {
            position: relative;
           
    }
    .capital_sec {
        margin: 120px 0px;
    }
    .capi_img
    {
