    }
    .step_fund {
        margin-top: 50px;
    }
    .step_fund .number
    {
            margin: 0px auto;
    }
    .step_box
    {
            text-align: center;
    }
    .step_fund .step_box {
     margin-bottom: 50px;
        padding: 0px 0px;
    }
    .feature_inner
    {
            text-align: center;
    }
    .feature_box .icon
    {
            margin: 0px auto;
    }
    .feature_box
    {
            padding-right: 0px;
            margin-bottom: 50px;
    }
    .tool_sec {
        margin: 100px 0px;
    }
    .tool_sec h2
    {
            margin-bottom: 50px;
    }
    .subcription_sec
    {
         
            padding: 100px 0px;
            padding-bottom: 200px;
    }
    .subscription_member {
          margin-top: 140px;
    }
    .dollar_box
    {
            top: -90px;
            width: 250px;
    }
    .dollar_box span
    {
                top: 76px;
    font-size: 90px;
        line-height: 90px;
    }
    .memberdata img {
        width: 55px;
    }
    .memberdata:after
    {
                top: 28px;
    }
    .invester_box
    {
            padding: 20px;
            margin-bottom: 35px;
    }
    .image_main
    {
            padding: 0px;
        padding-left: 100px;
        padding-bottom: 30px;
    }
    .card-header .btn-link:after
    {
                top: 50px;
    }
    .card-header .btn-link:not(.collapsed):after
    {
         top: 50px;     
    }
    .report_sec
    {
            background: rgb(68,53,146);
        background: linear-gradient(
    0deg
    , rgba(68,53,146,1) 0%, rgba(22,13,87,1) 16%, rgba(28,52,132,1) 33%, rgba(7,73,101,1) 48%, rgba(23,55,119,1) 60%, rgba(39,58,120,1) 77%, rgba(52,36,122,1) 94%);
    }
    
    footer {
        padding: 80px 0px;
    }
    
    .border_inner
    {
            width: 100%;
            height: 70%;
            top: 0%;
    }
    
    
    .mobile_main_banner_img {
            /*CSS transitions*/
            -o-transition-property: none !important;
            -moz-transition-property: none !important;
            -ms-transition-property: none !important;
            -webkit-transition-property: none !important;
            transition-property: none !important;
            /*CSS transforms*/
            -o-transform: none !important;
            -moz-transform: none !important;
            -ms-transform: none !important;
            -webkit-transform: none !important;
            transform: none !important;
            /*CSS animations*/
            -webkit-animation: none !important;
            -moz-animation: none !important;
            -o-animation: none !important;
            -ms-animation: none !important;
            animation: none !important;
            opacity: 1!important;
        }
    
    }
    
    
    
    
    
    
    
    
